import {useEffect, useState} from "react";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faAngleDown, faAngleUp} from "@fortawesome/free-solid-svg-icons";

export const ScrollToTop = () => {
	const [top, setTop] = useState(true)
	const [anim, setAnim] = useState(true)

	const scrollListener = () => {
		setTop(window.scrollY < 1500)
		setAnim(window.scrollY < 200)
	}

	// const clickListener = () => {
	// 	console.log(window.location.pathname=="/")
	// }

	useEffect(() => {
		document.addEventListener('scroll', scrollListener)
		// document.addEventListener('click', clickListener)
		return () => {
			document.removeEventListener('scroll', scrollListener);
		}
	}, [])

	return (
		<button
			className={`scroll-to-top rounded-circle${anim ? ' animated' : ''}`}
			style={anim ? {right: '30vw'} : {}}
			onClick={(e) => {
				e.preventDefault();
				if (top) {
					window.scrollTo(0, window.scrollY + 700)
					setTop(false);
				} else {
					window.scrollTo(0, 0)
					setTop(true);
				}
			}}
		>
			<FontAwesomeIcon icon={top ? faAngleDown : faAngleUp} size="2x"/>
		</button>
	)
}
