import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload, faEye} from "@fortawesome/free-solid-svg-icons";
import {socialLinks} from "../../utils";
import {useEffect, useState} from "react";

const TypeWriterIntro = () => {
	const positions = [
		'An Engineer',
		'A Software Developer',
		'A System Architect',
		'An AI Enthusiast',
		'A Graphics Designer',
		'A 3D Designer'
	];
	const [idx, setIdx] = useState(0);
	const [inc, setInc] = useState(true);
	useEffect(() => {
		if (inc) {
			setTimeout(() => {
				if (idx < positions.length - 1) setIdx(idx + 1); else setIdx(0);
				setInc(false)
			}, 2200)
		} else {
			setTimeout(() => {
				setInc(true)
			}, 50)
		}

	}, [inc])
	return (
		<>
			<h3 className="d-flex align-items-center mb-5">
				I'm&nbsp;
				<div
					className={`text-secondary overflow-hidden d-inline-block${inc ? " typewriter" : " d-none"}`}
				>{positions[idx]}</div>
			</h3>
		</>
	)
}

export default () => {
	let intro = `I am an electronics and communication Engineer and an enthusiast who adores nearly everything identified
    with innovation. With over 5 years of dynamic contribution in programming, robotics, data science, AI, ML, and
    automation, I have prepared myself as an ideal student and a professional. Functioning as a software engineer
    and an embedded systems programmer, I have taken in a great deal about the patterns and upgraded my demonstrable
    skill appropriately.`
	return (
		<div className="section-image-bg">
			<img className="section-background" src="/img/intro.jpg" alt=""/>
			<section className="resume-section p-3 p-lg-5 d-flex align-items-center" id="about">
				<div className="w-100">
					<h1 className="text-primary">Er. Sudip <span className="text-accent">Ghimire</span></h1>
					<TypeWriterIntro/>
					<a
						href="/res/Sudip_Ghimire_Resume.pdf"
						target="_blank"
						className="btn btn-outline-primary site-button mb-5 mx-3 d-inline-flex align-items-center"
					>
						<FontAwesomeIcon size="2x" icon={faEye} className="mx-2"/> View My Resume
					</a>
					<a
						href="/res/Sudip_Ghimire_Resume.pdf"
						target="_blank"
						download="sudip-ghimire-resume.pdf"
						className="btn btn-primary site-button mb-5 mx-3 d-inline-flex align-items-center"
					>
						<FontAwesomeIcon size="2x" icon={faDownload} className="mx-2"/> Download My Resume
					</a>
					{/*<a href="/res/Sudip_Ghimire_CV.pdf" className="btn btn-primary site-button mb-5 mx-3">*/}
					{/*  <FontAwesomeIcon size="2x" icon={faDownload}/> My Detailed CV*/}
					{/*</a>*/}
					<p className="lead mb-5 text-justify">{intro}</p>
					<div className="social-icons">
						{socialLinks.map(({url, icon}, key) => (
							<a className="m-3" key={key} href={url} target="_blank" rel="noopener noreferrer">
								<FontAwesomeIcon icon={icon} transform={{size: 24}}/>
							</a>
						))}
					</div>
				</div>
			</section>
			<div style={{
				position: 'relative',
				margin: "20px",
				paddingBottom: '56.25%',
				paddingTop: '25px',
				height: 0
			}}>
				<iframe
					style={{position: 'absolute', top: 0, left: 0}}
					title="Intro Video" width="100%" height='100%'
					// src="https://www.youtube.com/embed/UnHriYVfSzg?version=3&autoplay=0&controls=0&&showinfo=0&loop=1"
					src="https://www.youtube.com/embed/UnHriYVfSzg"
					frameBorder="0"
					allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
					allowFullScreen
				/>
				<hr className="mt-3 mb-3"/>
			</div>
		</div>
	);
}
