import { Component } from 'react';

export default class Interests extends Component {
  render() {
    return (
      <div>
        <section className="resume-section p-3 p-lg-5 d-flex align-items-center" id="interests">
          <div className="w-100">
            <h2 className="mb-5">Interests</h2>
            <p>
              I am interested in the projects related to Computer Vision, Human-Computer Interaction,
              Robotics and intelligent systems. I love sketching the spacecrafts, Space stations, and satellites
              in 3D CAD software, and animating them.
            </p>
            <p>During leisure, I enjoy most of the nights looking at the stars.
              I've made some cardboard telescopes just to see the beauty of the sky. I love photographing
              stars and constellations.</p>
            <p className="mb-0">I love watching Space documentaries and Sci-fi movies. I never miss an opportunity to
              watch anything related to Robots, Astronautics, and Autonomous Systems</p>
          </div>
        </section>
        < hr className = "m-0" />
      </div>
    );
  }
}