import {NavLink, useHistory} from "react-router-dom";
import React from "react";
import {SidebarItemInterface} from "./types";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBars, faTimes} from "@fortawesome/free-solid-svg-icons";
import {Simulate} from "react-dom/test-utils";

export const Sidebar = (props: { collapsed?: boolean, onCollapse: Function, children: any }) => {
	return (
		<nav className={`sidebar shadow bg-primary-gradient${(props.collapsed ?? false) ? " sidebar-sm" : ""}`}>
      <span className="pin-icon" onClick={(e) => {
				props.onCollapse();
			}}>
							<FontAwesomeIcon icon={props.collapsed ? faBars : faTimes} transform={{size: 22}}/>
      </span>
			{props.children}
		</nav>
	)
}

export const SidebarItem = (props: SidebarItemInterface) => {
	const {icon, children, link, className, ...extraProps} = props
	const history = useHistory();
	return (
		<>
			<NavLink className={`sidebar-item ${props.className ?? ""}`} to={link} {...extraProps}>
				<span className="sidebar-icon"><FontAwesomeIcon icon={icon} transform={{size: 20}}/></span>
				<span className="sidebar-body">{children}</span>
			</NavLink>
		</>
		// </div>
	)
}
