import './App.css';
import 'bootstrap';
import './scss/style.scss';
import {ScrollToTop, Navbar} from "./components";
import {BrowserRouter, Route, Switch} from "react-router-dom";
import routes from "./routes";
import Blog from "./pages/blog";
import Contact from "./pages/contact";
import {Gallery} from "./pages/Gallery";
import AboutMe from './pages/AboutMe';
import {useEffect} from "react";
import {LectureNotes} from './pages/LectureNotes';

const App = () => {
	const rightClickCanceller = (e: MouseEvent) => {
		// console.log(e.button);
		e.preventDefault();
		e.stopImmediatePropagation();
	}
	useEffect(() => {
		window.addEventListener('contextmenu', rightClickCanceller);
		return () => {
			window.removeEventListener('contextmenu', rightClickCanceller)
		}
	}, [])
	return (
		<>
			<BrowserRouter>
				<Navbar/>
				<Switch>
					<>
						<Route exact path={routes.index} component={AboutMe}/>
						<Route exact path={routes.blog} component={Blog}/>
						<Route exact path={routes.contact} component={Contact}/>
						<Route exact path={routes.gallery} component={Gallery}/>
						<Route exact path={routes.notes} component={LectureNotes}/>
					</>
				</Switch>
			</BrowserRouter>
		</>
	);
}

export default App;
